const proxyURL = 'https://cors-anywhere.herokuapp.com/';
const targetURL = 'https://megapolis.bpium.ru/api/v1';
const URL = proxyURL + targetURL;


export default async function(path, user) {
  let headers = {
    "Content-Type": "application/json",
  };

  //
  if (user) headers["Authorization"] = "Basic " + btoa(`${user.email}:${user.password}`);

  let res = await fetch(URL + path, {
    method: "GET",
    headers,
  });

  return res;
}
